import React, { Component } from 'react'
import $ from 'jquery'
import url from './audio/gbqq.mp3' // 引入背景音乐文件


class Main extends Component {
   
    componentDidMount() {
        this.print();
       
        var audio = document.getElementById("audio");
        setTimeout(() => audio.play(), 8500) // 背景音乐在页面加载后，延迟播放的时长，单位：毫秒。
    }
    print = () => {
        $.fn.autotype = function () {
            var _this = $(this);
            var str = _this.html();
            // 正则替换代码行之间添加的多个空格，不去除换行输出会有明显的停顿：实际是在输出多个空格
            str = str.replace(/(\s){2,}/g, "$1");
            var index = 0;
            $(this).html('');
            var timer = function fn() {
                var args = arguments;
                var current = str.slice(index, index + 1);
                // html标签完整输出,如：<p>
                if (current == '<') {
                    index = str.indexOf('>', index) + 1;
                }
                else {
                    index++;
                }
                //位运算符: 根据setInterval运行奇偶次来判断是否加入下划线字符“_”，使输入效果更逼真
                if (index < str.length - 1) { //打印字符倒数第2个字符开始，不加下划线字符，以防止结束符可能会多输出一下划线字符
                    _this.html(str.substring(0, index) + (index & 1 ? '_' : ''));
                } else {
                    _this.html(str.substring(0, index));
                    clearTimeout(timer);
                };
                setTimeout(fn, 200)
            };
            // 延迟1s开始
            setTimeout(timer, 1000);
        };
        $("#autotype").autotype();
    }
  
    render() {
       
        return (
            <div className="App animated bounceInLeft">
    
                <div id="autotype">
                    <h1 style={{ fontWeight: 900 }}>嗨！猪猪女孩嘤嘤嘤</h1>
                    <p >在煽情开始之前，先放首歌当背景音乐吧！Music!</p>
                    <p>我们一起经历了许许多多的事情，有欢笑也有争吵，但是我们都走过来了，这还是我第一次给你过生日</p>
                    <p>蓦然回首，真的已经过了好久好久，一切又是那么的匆忙，我们都已经站在了人生的路口上，正在向我们所向往的路上携手并进，
					  我们时吵时闹，时哭时笑，你依旧在我身旁，千言万语道不尽，脉脉含情在眼中。希望一路有你，一辈子（一被子）哈哈
                </p>
                    
                    <p>最后祝宝贝儿日快乐哦！</p>
                    <div style={{ textAlign: 'right' }}>
                        <p>爱你的♥猪猪男孩</p>
                        <p>2019年8月29日</p>
                    </div>
                </div>
                <audio id="audio" src={url}></audio>
            </div>

        )
    }
}
export default Main



